import { OrbitControls, TransformControls, PivotControls,
         Html, Text, Float, MeshReflectorMaterial } from '@react-three/drei'
import { useRef } from 'react'
import { DoubleSide } from 'three'

export default function Experience() {

    const cube = useRef()
    const sphere = useRef()
    const diamond = useRef()
    
    return <>

        <OrbitControls makeDefault />
        
        <directionalLight position={ [ 1, 2, 3 ] } intensity={ 1.5 } />
        <ambientLight intensity={ 0.5 } />

        <PivotControls anchor={[0, 0, 0]} depthTest={false}
            axisColors={['#9381ff', '#ff4d6d', '#7ae582']} scale={ 1.5 }>
                <mesh ref={sphere} position-x={ - 3 } position-z={ -1.5 }>
                    <sphereGeometry />
                    <meshStandardMaterial color="orange" />
                    <Html position={[1, 1, 0]} 
                    wrapperClass="label" 
                    center distanceFactor={6}
                    occlude={[sphere, diamond, cube]}>Sphere </Html>
                </mesh>
        </PivotControls>

        <PivotControls anchor={[0, 0, 0]} depthTest={false}
            axisColors={['#5581ff', '#ff4d6d', '#f6e58d']} scale={ 1.5 }>
                <mesh ref={ cube } position-x={ 2 } position-z={ -1.5 } scale={ 1.5 }>
                    <boxGeometry />
                    <meshStandardMaterial color="mediumpurple" />
                    <Html position={[0.8, 0.8, 0]} 
                    wrapperClass="label" 
                    center distanceFactor={6}
                    occlude={[sphere, diamond, cube]}>Cube </Html>
                </mesh>
                {/* <TransformControls object={ cube } mode="translate" /> */}
        </PivotControls>

        <PivotControls anchor={[0, 0, 0]} depthTest={false}
            axisColors={['#576574', '#dcdde1', '#D6A2E8']} scale={ 1.2 }>
                <mesh ref={diamond} position-z={ 3 }>
                    <sphereGeometry args={ [1, 4, 2] }/>
                    <meshStandardMaterial color="#2e86de" />
                    <Html position={[1, 1, 0]} 
                    wrapperClass="label" 
                    center distanceFactor={6}
                    occlude={[sphere, diamond, cube]}>Diamond </Html>
                </mesh>
        </PivotControls>

        <mesh position-y={ - 1 } rotation-x={ - Math.PI * 0.5 } scale={ 15 }>
            <planeGeometry />
            {/* <meshStandardMaterial color="greenyellow" /> */}
            <MeshReflectorMaterial color="#b8e994" resolution={512}/>
        </mesh>

        <Float speed={2.5} floatIntensity={0.8}>
            <Text font="./bangers-v20-latin-regular.woff"
            fontSize={ 1 } color="#eb4d4b" position={[0, 3, -4]} maxWidth={3}>
                play with objects
                <meshNormalMaterial side={ DoubleSide }/>
            </Text>
        </Float>
    </>
}